import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { AppRoutingModule } from './app-routing.module';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { FileUploadModule } from 'ng2-file-upload';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { RegisterComponent } from './components/register/register.component';
import { LoginComponent } from './components/login/login.component';
import { ProfileComponent } from './components/profile/profile.component';
import { BlogComponent } from './components/blog/blog.component';
import { EditBlogComponent } from './components/blog/edit-blog/edit-blog.component';
import { ProductsComponent } from './components/products/products.component';

import { AuthService } from './services/auth.service';
import { BlogService } from './services/blog.service';
import { ProductService } from './services/product.service';
import { AuthGuard } from './guards/auth.guard';
import { NotAuthGuard } from './guards/notAuth.guard';
import { AdminGuard} from './guards/admin.guard';
import { InitializePopupDirective } from './directives/initialize-popup.directive';
import { DeleteBlogComponent } from './components/blog/delete-blog/delete-blog.component';
import { PublicProfileComponent } from './components/public-profile/public-profile.component';
import { InitializeDropdownDirective } from './directives/initialize-dropdown.directive';


@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    DashboardComponent,
    RegisterComponent,
    LoginComponent,
    ProfileComponent,
    ProductsComponent,
    BlogComponent,
    InitializePopupDirective,
    EditBlogComponent,
    DeleteBlogComponent,
    PublicProfileComponent,
    InitializeDropdownDirective
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    AppRoutingModule,
    FlashMessagesModule,
    FileUploadModule
  ],
  providers: [AuthService, AuthGuard, NotAuthGuard, AdminGuard, BlogService, ProductService],
  bootstrap: [AppComponent]
})
export class AppModule { }



